<template>
  <div class="hello">
    <Header />
    <div class="body">
      <div v-if="!isValidURL" class="desc">错误的分享内容</div>
      <div v-else-if="isOpeningApp" class="desc">正在打开App...</div>
      <div v-else class="addButton" v-on:click="addSource">点击添加源到Flow</div>
      <div>如果没有安装，请点击下面按钮</div>
      <a class="appstoreLink" href="https://apps.apple.com/app/flow-media-browser/id1548245678">
        <img class="appstore" alt="Vue logo" src="../assets/appstore.png" />
      </a>
      <div class="tf">Early access is opening now</div>
      <div class="sepline"></div>
      <img class="screenshot" alt="Vue logo" src="../assets/screenshot.png" />
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
export default {
  name: "FlowAPP",
  components: {
    Header,
    Footer,
  },
  props: {},
  data: function() {
    return {
      isValidURL: true,
      isOpeningApp: true,
    };
  },
  methods: {
    addSource: function() {
    var href = new URL(window.location.href);
    var url = href.searchParams.get("url");
    if (url && url.startsWith("flow://")) {
      this.isOpeningApp = true
      window.location.href = url;
      setTimeout(() => {
        this.isOpeningApp = false;
      }, 3000);
    }
  },
  },
  mounted: function() {
    var href = new URL(window.location.href);
    var url = href.searchParams.get("url");
    console.log(url)
    if (url && url.startsWith("flow://")) {
      window.location.href = url;
      setTimeout(() => {
        this.isOpeningApp = false;
      }, 3000);
    } else {
      this.isValidURL = false;
      this.isOpeningApp = false;
    }
  },
};
</script>

